import { Routes, Route, Navigate } from "react-router-dom";
import Layout from "./components/Layout";
import Home from "./pages/Home";
import About from "./pages/About";
import Collection from "./pages/Collections";
import Contact from "./pages/Contact";
import Stone from "./pages/Stone";
import ScrollToTop from './components/ScrollToTop';
import Impressum from "./pages/Impressum";
import Datenschutz from "./pages/Datenschutz";
import AGB from "./pages/AGB";

export default function App() {
  return (
    <>  // Neu: Fragment als Parent-Wrapper (fix fr den Fehler)
      <ScrollToTop />
      <Layout>
        <Routes>
          <Route path="/" element={<Navigate to="/Home" replace />} />  // Redirect von Root
          <Route path="/Home" element={<Home />} />
          <Route path="/Collection" element={<Collection />} />
          <Route path="/About" element={<About />} />
          <Route path="/Contact" element={<Contact />} />
          <Route path="/Stone" element={<Stone />} />  // Bleibt gleich  Query-Params (?id=1) funktionieren automatisch
          <Route path="/Impressum" element={<Impressum />} />
          <Route path="/Datenschutz" element={<Datenschutz />} />
          <Route path="/AGB" element={<AGB />} />
          <Route path="*" element={<div>404 - Seite nicht gefunden</div>} />  // Fallback
        </Routes>
      </Layout>
    </>
  );
}
